.container {
    width: 320px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 20px;
    padding-left: 20px;

    // @media screen and (min-width: 480px) {
    //     width: 480px;
    // }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-right: 32px;
        padding-left: 32px;
    }

    @media screen and (min-width: 1024px) {
        width: 1024px;
    }
}